import { type } from "@testing-library/user-event/dist/type";
import React, { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import Nav from 'react-bootstrap/Nav';
import { useSSRSafeId } from "@react-aria/ssr";
import { concatData } from './../store/cartData.js'
import { useDispatch } from "react-redux";

let YellowButton = styled.button`
  background: ${props => props.bg};
  color: ${props => props.bg == 'blue' ? 'white' : 'black'};
  padding: 10px;
`

var a = 0;
function Detail(props) {
  
  for(var i = 0; i <10000000; i++){
    a = i
  }

  let dispatch = useDispatch();
  let [hide, setHide] = useState(true);
  let [count, setCount] = useState(0);
  let [inputValue, setInputValue] = useState(0);
  let [tab, setTab] = useState(0)

  let [detailHide, setDetailHide] = useState("");
  useEffect(()=>{
    setTimeout(()=>{setDetailHide("end")}, 100)
    return(()=>{
      setDetailHide("")
    })
  }, [])

  useEffect(() => {
    let timmer = setTimeout(() => { setHide(false) }, 2000)
    return () => {
      clearTimeout(timmer)

    }
  }, [])

  let { id } = useParams();

  useEffect(()=>{
    let watchedItems = JSON.parse(localStorage.getItem('watched'))
    if (watchedItems == undefined){
      localStorage.setItem('watched', JSON.stringify([id]))
    }
    else if (watchedItems.includes(String(id)) == []) {
      let newItems = [...watchedItems, id]
      localStorage.setItem('watched', JSON.stringify(newItems))
    }
  }, [])
  
  let shoesData = props.shoes.find(function (x) {
    return x.id == id
  })
  console.log(props.shoes)
  if (shoesData === undefined) {
    return (
      <div> 없는 제품입니다. </div>
    )
  }
  else {
    return (
      <div className={"container start " + detailHide} >
        {hide ? <Hide /> : null}
        <button onClick={() => { setCount(count + 1) }}>버튼</button>
        {/* <YellowButton bg="blue">버튼</YellowButton> */}
        <div className="row">
          <div className="col-md-6">
            <img src={"https://codingapple1.github.io/shop/shoes" + (Number(id) + 1) + ".jpg"} width="100%" />
          </div>
          {isNaN(inputValue) ? <div style={{ backgroundColor: "red", color: "white" }}>숫자만 입력가능합니다. </div> : null}
          <input onChange={(e) => { setInputValue(e.target.value) }} />

          <div className="col-md-6">
            <h4 className="pt-5">{shoesData.title}</h4>
            <p>상품설명</p>
            <p>{shoesData.price}원</p>
            <button className="btn btn-danger" onClick={()=>{
              dispatch(concatData(shoesData))
            }}>주문하기</button>
          </div>
        </div>

        <Nav variant="tabs" defaultActiveKey="link-0">
          <Nav.Item>
            <Nav.Link eventKey="link-0" onClick={() => { setTab(0) }}>상품명</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1" onClick={() => { setTab(1) }}>버튼1</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-2" onClick={() => { setTab(2) }}>버튼2</Nav.Link>
          </Nav.Item>
        </Nav>
        <TabContent tab={tab} shoes={props.shoes} />
        

      </div>
    )
  }
}

function TabContent({tab, shoes}){
  let [fade, setFade] = useState("")
  useEffect(()=>{
    setTimeout(()=>{setFade("end")}, 100)
    return () =>{
      setFade("")
    }
  }, [tab])

  return (<div className={"start " + fade}>
    {[<div>{shoes[0].title}</div>, <div>내용1</div>, <div>내용2</div>][tab]}
  </div>)
  
}

function Hide() {
  return (
    <div className="alert alert-warning">
      2초이내 구매시 할인
    </div>
  )
}




export default Detail;